body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  font-family: 'Montserrat', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

button {
  border: solid 3px #7fc4f0;
  outline: none;
  padding: 10px 20px;
  border-radius: 3px;
  background-color: white;
  font-weight: 700;
  color: #7fc4f0;
  cursor: pointer;
}

button:hover {
  opacity: 0.9;
}
.Cell {
  background-color: lightskyblue;
  width: 25%;
  height: 25%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 24px;
  font-weight: 700;
  border-radius: 8px;
  border: solid 4px white;
  color: white;
  text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.15);
  position: absolute;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-sizing: border-box;
  transition: all 0.1s ease-in-out;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent;
}

.Cell:hover {
  background-color: #7fc4f0;
}

.Cell.empty {
  opacity: 0;
  cursor: default;
  /*background-color: cornflowerblue;*/
  /*box-shadow: inset 0 0px 5px rgba(0, 0, 0, 0.2);*/
}
.Grid {
  width: 300px;
  height: 300px;
  position: relative;
  border: solid 3px lightskyblue;
  border-radius: 5px;
  margin: auto;
}

@media screen and (max-width: 600px) {
  .Grid {
    width: 250px;
    height: 250px;
  }
}
.Game {

}

.Game h1 {
  color: #7fc4f0;
  text-align: center;
}

.GameActions {
  margin: 40px auto auto;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  width: 300px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.GameActions button {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.GameActions button:not(:last-child) {
  margin-right: 5px;
}


@media screen and (max-width: 600px) {
  .GameActions {
    width: 250px;
  }
}

.GameCongratulations {
  text-align: center;
  margin: 40px 0;
  color: #ff6600;
  font-weight: 500;
}
